//     
export const getPriceHistory = async (symbol, days = 1) => {
  try {
    const coinId = coinIdMap[symbol.toUpperCase()] || 'the-open-network';
    
    //       API
    // const response = await fetch(`https://api.coingecko.com/api/v3/coins/${coinId}/market_chart?vs_currency=usd&days=${days}`);
    // const data = await response.json();
    // return data.prices;
    
    //     
    return generateMockPriceData(days);
  } catch (error) {
    console.error('    :', error);
    return []; //      
  }
};

//     
const generateMockPriceData = (days) => {
  const data = [];
  const now = Date.now();
  const intervalHours = 1; //     
  const interval = (24 * 60 * 60 * 1000) / (24 / intervalHours); //   
  const points = days * 24 / intervalHours; //  
  
  let lastPrice = 2.5; //  
  
  for (let i = 0; i < points; i++) {
    //    
    const change = (Math.random() - 0.5) * 0.03; //     1.5%
    lastPrice = Math.max(0.1, lastPrice + lastPrice * change); //      0.1
    
    //   [timestamp, price]
    data.push([now - (points - i) * interval, lastPrice]);
  }
  
  return data;
};

//        
export const getTokenPriceData = async (symbol) => {
  const price = await getCurrentPrice(symbol);
  const change24h = await getPriceChange24h(symbol);
  
  return {
    price,
    change24h
  };
}; 