/**
 *     API 
 *       
 */

//  URL API
const API_BASE_URL = 'http://91.186.196.11:9000/api/v1';

/**
 *   URL
 * @param {string} url - URL  
 * @returns {boolean}  
 */
const isValidUrl = (url) => {
  try {
    new URL(url);
    return true;
  } catch (e) {
    return false;
  }
};

/**
 *   fetch    
 * @param {string} url - URL  
 * @param {Object} options -  
 * @returns {Promise}  
 */
const safeFetch = async (url, options = {}) => {
  try {
    //   URL
    if (!isValidUrl(url)) {
      throw new Error(` URL: ${url}`);
    }
    
    //  ,     
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 10000); // 10  
    
    const response = await fetch(url, {
      ...options,
      signal: controller.signal
    }).finally(() => clearTimeout(timeoutId));
    
    return response;
  } catch (error) {
    //    
    if (error.name === 'AbortError') {
      throw new Error('   ');
    }
    
    if (error.message.includes('Failed to fetch')) {
      throw new Error(`    : ${url}.       .`);
    }
    
    throw error;
  }
};

/**
 *    QR-  
 * @param {string} qrData -   QR-
 * @param {number} userId - ID 
 * @returns {Promise}    
 */
export const sendQrCodeData = async (qrData, userId = 1) => {
  try {
    //   
    if (!qrData) {
      throw new Error('QR-    ');
    }
    
    if (typeof userId !== 'number' || isNaN(userId)) {
      console.warn('ID    ,    ');
      userId = 1;
    }
    
    const fullUrl = `${API_BASE_URL}/orders`;
    
    const response = await safeFetch(fullUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        user_id: userId,
        qr: qrData
      })
    });

    if (!response.ok) {
      //       
      let errorMessage;
      try {
        const errorData = await response.json();
        errorMessage = errorData?.message;
      } catch (e) {
        errorMessage = `    QR-.  : ${response.status}`;
      }
      
      throw new Error(errorMessage);
    }

    //   JSON
    try {
      return await response.json();
    } catch (e) {
      console.warn('   JSON ', e);
      return { success: true }; //    ,     JSON
    }
  } catch (error) {
    console.error('    QR-:', error);
    
    //         
    //   ,    
    return {
      success: false,
      error: error.message || '      QR-',
      timestamp: new Date().toISOString()
    };
  }
};

/**
 *   
 * @returns {Promise<boolean>}    
 */
export const checkServerStatus = async () => {
  try {
    const fullUrl = `${API_BASE_URL}/status`;
    
    const response = await safeFetch(fullUrl, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
    });
    
    return response.ok;
  } catch (error) {
    console.error('    :', error);
    return false;
  }
};

export default {
  sendQrCodeData,
  checkServerStatus
}; 