import { useState } from 'react';
import { sendQrCodeData } from '../services/apiService';

/**
 *     QR      
 * @param {Object} options -   
 * @param {Function} options.onScanSuccess - ,    
 * @param {Function} options.onScanError - ,    
 * @param {Function} options.onApiSuccess - ,       
 * @param {Function} options.onApiError - ,       
 * @returns {Object}         QR 
 */
const useQrScanner = ({
  onScanSuccess,
  onScanError,
  onApiSuccess,
  onApiError
} = {}) => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [scanResult, setScanResult] = useState(null);

  /**
   *    QR-
   * @param {string} qrData -   QR-
   */
  const handleScan = (qrData) => {
    try {
      setScanResult(qrData);
      
      //    ,   
      if (onScanSuccess) {
        onScanSuccess(qrData);
      }
      
      return qrData;
    } catch (err) {
      setError(err.message || '    QR-');
      
      if (onScanError) {
        onScanError(err);
      } else {
        console.error('   QR-:', err);
      }
      
      //  null   
      return null;
    }
  };
  
  /**
   *   QR-  
   * @param {string} qrData -   QR-
   * @param {number} userId - ID  (  1)
   */
  const sendToServer = async (qrData, userId = 1) => {
    if (!qrData) {
      setError('QR-    ');
      
      if (onApiError) {
        onApiError(new Error('QR-    '));
      }
      
      return { success: false, error: 'QR-    ' };
    }
    
    try {
      //   
      setIsLoading(true);
      setError(null);
      
      //       API
      const result = await sendQrCodeData(qrData, userId);
      
      //   
      setIsLoading(false);
      
      //     
      if (!result.success && result.error) {
        setError(result.error);
        
        //    API,   
        if (onApiError) {
          onApiError(new Error(result.error));
        } else {
          console.error('     :', result.error);
        }
        
        return result;
      }
      
      //      ,   
      if (onApiSuccess) {
        onApiSuccess(result);
      }
      
      return result;
    } catch (err) {
      //    
      setIsLoading(false);
      setError(err.message || '      ');
      
      //    API,   
      if (onApiError) {
        onApiError(err);
      } else {
        console.error('     :', err);
      }
      
      //       
      return { 
        success: false, 
        error: err.message || '      ',
        timestamp: new Date().toISOString()
      };
    }
  };

  /**
   *    QR-
   * @param {Error} error -  
   */
  const handleScanError = (error) => {
    setError(error.message || '   QR-');
    
    //    ,   
    if (onScanError) {
      onScanError(error);
    } else {
      console.error('   QR-:', error);
    }
  };

  /**
   *   
   */
  const resetScanner = () => {
    setIsLoading(false);
    setError(null);
    setScanResult(null);
  };

  return {
    isLoading,
    error,
    scanResult,
    handleScan,
    sendToServer,
    handleScanError,
    resetScanner
  };
};

export default useQrScanner; 